<template>
  <div class="play-control">
    <el-row type="flex" justify="center">
      <el-col :span="4" class="control-icon control-col">
        <i class="el-icon-caret-left"></i>
        <i class="el-icon-video-play" v-show="!playStatus" @click="play"></i>
        <i class="el-icon-video-pause" v-show="playStatus" @click="pause"></i>
        <i class="el-icon-caret-right"></i>
      </el-col>
      <el-col :span="12" class="control-col">
        <el-row class="control-concent">
          <el-col>{{songContent.song}} - {{songContent.singer}}</el-col>
        </el-row>
        <el-row type="flex" justify="center" class="play-bar">
          <el-col :span="17">
            <el-slider v-model="currentBar" :show-tooltip="false" @click.native="skipBar"></el-slider>
          </el-col>
          <el-col :span="4">{{durationText}}/{{currentText}}</el-col>
        </el-row>
      </el-col>
      <el-col :span="3" class="control-icon control-col">
        <i class="el-icon-more"></i>
      </el-col>
    </el-row>
    <audio ref="audio" autoplay></audio>
  </div>
</template>

<script>
export default {
  data() {
    return {
      playStatus: false,
      Timer: "", //定时器
      currentBar: 0,
      currentText: "00:00",
      durationText: "00:00"
    };
  },
  watch: {
    songContent() {
      this.playSong(this.songContent.id);
    }
  },
  computed: {
    songContent() {
      return this.$store.state.playContent;
    }
  },
  methods: {
    //点击播放
    play() {
      this.$refs.audio.ended
        ? this.playSong(this.songContent.id)
        : this.$refs.audio.play(),
        this.setTimer();
      this.playStatus = true;
    },
    //点击暂停
    pause() {
      this.playStatus = false;
      this.$refs.audio.pause(), this.clearTimer();
    },
    //初始化播放歌曲
    playSong(id) {
      this.durationText = this.songContent.dt;
      this.playStatus = true;
      this.currentText = "00:00";
      this.currentBar = 0;
      let song_url = "https://api.imjad.cn/cloudmusic/?type=song&id=" + id;
      this.getSong(song_url);
    },
    //获取歌曲信息并播放
    getSong(url) {
      this.$axios
        .get(url)
        .then(
          res => (
            (this.$refs.audio.src = res.data.data[0].url), this.setTimer()
          )
        )
        .catch(function(err) {
          console.log(err);
        });
    },

    //进度条跳转
    skipBar() {
      this.$refs.audio.currentTime =
        (this.currentBar / 100) * this.$refs.audio.duration;
    },
    //格式化播放时间
    formatTime(string) {
      var m = parseInt(string / 60);
      var s = parseInt(string % 60);
      m >= 10 ? m : (m = "0" + m);
      s >= 10 ? s : (s = "0" + s);
      return m + ":" + s;
    },
    //更新进度条
    updateCurrent() {
      this.currentBar == 100
        ? ((this.playStatus = false), this.clearTimer())
        : (this.currentBar =
            (this.$refs.audio.currentTime / this.$refs.audio.duration) * 100);
      this.currentText = this.formatTime(this.$refs.audio.currentTime);
      this.$store.commit("setCurrent", this.$refs.audio.currentTime);
    },
    //设置定时器
    setTimer() {
      this.clearTimer();
      this.Timer = setInterval(this.updateCurrent, 1000);
    },
    //清除定时器
    clearTimer() {
      clearInterval(this.Timer);
      this.Timer = "";
      console.log("clearTimer");
    }
  }
};
</script>

<style scoped>
.play-control {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background: rgba(200, 200, 200, 0.5);
  transition: 0.3s;
  z-index: 999;
}
.play-control:hover {
  background: rgba(150, 150, 150, 0.8);
}
.control-icon {
  padding-top: 0.5rem;
  font-size: 2.5rem;
  opacity: 0.8;
}
.control-col {
  margin-top: 1rem;
}
.el-progress {
  display: flex;
}
.el-progress div {
  padding: 0.2rem;
  color: white;
}
.play-bar .el-col:last-child {
  margin-left: 0.8rem;
  display: flex;
  align-items: center;
}
</style>


