<template>
  <div class="template">
    <el-row class="banner">
      <transition name="el-fade-in-linear">
        <el-col :span="24" v-show="bannerShow" class="banner-img">
          <img src="@/assets/banner/ArticleBanner.jpg" />
        </el-col>
      </transition>
      <transition name="el-fade-in-linear">
        <el-col class="banner-header" v-show="bannerShow">
          <el-row type="flex" align="center">
            <el-col>用文字记录生活</el-col>
          </el-row>
          <el-row type="flex" justify="center" class="banner-btn">
            <el-col :span="4">
              <el-button type="primary">技术</el-button>
            </el-col>
            <el-col :span="4">
              <el-button type="success">生活</el-button>
            </el-col>
            <el-col :span="4">
              <el-button type="danger">诗歌</el-button>
            </el-col>
            <el-col :span="4">
              <el-button type="warning">杂文</el-button>
            </el-col>
          </el-row>
        </el-col>
      </transition>
    </el-row>
    <el-row class="container" type="flex" justify="center">
      <el-col :span="14" class="content">
        <el-row>
          <el-col class="content-title">这是一篇文章噢</el-col>
        </el-row>
        <el-row>
          <el-col :span="3" class="content-tag">
            <i class="el-icon-collection-tag icon-size">博文</i>
          </el-col>
          <el-col :span="3" class="content-tag">
            <i class="el-icon-collection-tag icon-size">生活</i>
          </el-col>
          <el-col class="content-tag" :span="5">
            <i class="el-icon-time icon-size">2020-05-11</i>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row class="content-cover" @click.native="gotoArticlecontent">
          <el-col :span="24">
            <img src="@/assets/banner/MusicBanner.jpg" />
          </el-col>
        </el-row>
        <el-row class="content-lable">
          <el-col>这是文章的描述</el-col>
        </el-row>
        <el-divider></el-divider>
        <el-row type="flex" justify="end">
          <el-col :span="3" class="footer-icon">
            <i class="el-icon-view icon-size">1133</i>
          </el-col>
          <el-col :span="4" class="footer-icon">
            <i class="el-icon-chat-line-round icon-size">1533</i>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "articleComponent",
  data() {
    return {
      bannerShow: false
    };
  },
  methods: {
    gotoArticlecontent() {
       this.$router.push('/Articlecontent');
    }
  },
  mounted() {
    this.bannerShow = true;
  }
};
</script>

<style scoped>
.template {
  width: 100%;
  background: #f2f2f2;
}
.banner {
  height: 40rem;
  overflow: hidden;
  position: relative;
}
.banner-img {
  transition: 0.5s;
}
.banner-img img {
  width: 100% !important;
}
.banner-header {
  transition: 1s;
  position: absolute;
  font-size: 5rem;
  font-family: "STHupo", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  top: 30%;
  color: #E6A23C;
}
.banner-btn {
    margin-top: 3rem;
}
.el-button {
  font-size: 3vh;
  padding: 30px 60px;
  border-radius: 20px;
  transition: 0.8s;
  background: rgba(0, 0, 0, 0.3);
}
.el-button:hover {
  background: rgba(0, 0, 0, 0.9);
}

.content {
  margin: 5rem;
  background: white;
  border-radius: 1.2rem;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
}
.content:hover {
  box-shadow: 0 0 1.5rem rgb(150, 150, 150);
}
.el-divider {
  margin: 0;
}
.content .el-col {
  text-align: left;
}
.content-cover {
  flex: 1;
  margin-top: 1rem;
  overflow: hidden;
  cursor: pointer;
}
.content-cover .el-col {
  height: 100%;
  overflow: hidden;
}
.content-cover .el-col img {
  width: 100%;
}
.content-lable {
  font-size: 1.5rem;
  padding: 1rem;
  font-family: "STXingkai";
}
.content-title {
  padding-top: 1rem;
  padding-left: 1rem;
  font-family: "KaiTi";
  font-size: 2rem;
  font-weight: bold;
  color: rgb(50, 50, 50);
}
.content-tag {
  padding-left: 0.5rem;
}
.icon-size {
  font-size: 1rem;
  margin: 1rem 1rem;
  color: rgb(100, 100, 100);
}
.footer-icon {
  padding: .8rem;
}
</style>


